<template>
  <div class="home-page">
    <Card :textMode="true" v-for="(text, index) in texts" :key="index">
      <template #header> {{ textFileNames[index] }} </template>
      <template #content>
        {{ text.default }}
      </template>
    </Card>
  </div>
</template>

<script>
import Card from '@/components/mobile/CardItem.vue'
export default {
  components: {
    Card
  },
  data () {
    return {
      texts: [],
      textFileNames: []
    }
  },
  created () {
    this.loadTxtFiles()
  },
  methods: {
    loadTxtFiles () {
      const requireTxt = require.context('@/assets/daily-notes', false, /\.txt$/) // Ëé∑ÂèñËØ•Êñá‰ª∂Â§π‰∏≠ÁöÑÊâÄÊúâtxtÊñá‰ª∂
      this.textFileNames = requireTxt.keys()
      this.textFileNames = this.textFileNames.map(item => item.replace('.txt', ''))
      this.textFileNames = this.textFileNames.map(item => item.replace('./', ''))
      const texts = []
      requireTxt.keys().forEach((fileName) => {
        const fileContent = requireTxt(fileName) // ËØªÂèñÊñá‰ª∂ÂÜÖÂÆπ
        fileContent.default.trim()
        texts.push(fileContent) // Â∞ÜÂÜÖÂÆπÂ≠òÂÖ•Êï∞ÁªÑ
      })
      this.texts = texts // Â∞ÜÊâÄÊúâÊñáÊú¨ÂÜÖÂÆπËµãÂÄºÁªô data ‰∏≠ÁöÑ texts
    }
  }
}
</script>

<style lang="less" scoped>
.home-page {
  width: 100%;
}
</style>
