<template>
  <div class="home-page">
    <CardItem v-for="(imgSrc, index) in images" :key="index" :imgSrc="imgSrc" :textMode="false">
      <template #header>
        {{ imgSrc.replace(/\.[^/.]+$/, '') }}
      </template>
    </CardItem>
  </div>
</template>

<script>
import CardItem from '@/components/mobile/CardItem.vue'
export default {
  components: {
    CardItem
  },
  created () {
    this.loadImgFiles()
  },
  methods: {
    loadImgFiles () {
      // ‰ΩøÁî® require.context ËØªÂèñÊñá‰ª∂Â§π‰∏≠ÁöÑÊâÄÊúâÊñá‰ª∂
      const imagesContext = require.context('@/assets/imgs', false, /\.(png|jpe?g|gif)$/)

      // Ëé∑ÂèñÊñá‰ª∂Ë∑ØÂæÑÂàóË°®Âπ∂ËµãÂÄºÁªô images
      this.images = imagesContext.keys().map(imagePath => {
      // ‰ΩøÁî®Ê≠£ÂàôÊèêÂèñÊñá‰ª∂Âêç
        return imagePath.replace(/^.*[\\/]/, '')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.home-page {
  width: 100%;
}
</style>
