<template>
  <div class="home-page">
    <Card v-for="(imgSrc, index) in images" :key="index" :imgSrc="imgSrc" :textMode="false">
      <template #header>{{ imgSrc.replace(/\.[^/.]+$/, '') }}</template>
    </Card>
  </div>
</template>

<script>
import Card from '@/components/Device/CardItem.vue'
export default {
  components: {
    Card
  },
  data () {
    return {
      images: []
    }
  },
  created () {
    // ‰ΩøÁî® require.context ËØªÂèñÊñá‰ª∂Â§π‰∏≠ÁöÑÊâÄÊúâÊñá‰ª∂
    const imagesContext = require.context('@/assets/imgs', false, /\.(png|jpe?g|gif)$/)

    // Ëé∑ÂèñÊñá‰ª∂Ë∑ØÂæÑÂàóË°®Âπ∂ËµãÂÄºÁªô images
    this.images = imagesContext.keys().map(imagePath => {
      // ‰ΩøÁî®Ê≠£ÂàôÊèêÂèñÊñá‰ª∂Âêç
      return imagePath.replace(/^.*[\\/]/, '')
    })
  }
}
</script>

